@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500&family=Noto+Serif+KR:wght@300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Jost:wght@300;400;500&display=swap");

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
div,
form,
input,
textarea,
table,
td,
th,
header,
section,
aside,
footer,
article,
nav,
ul,
ol,
li {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  line-height: 1;
  font-size: inherit;
  font-weight: inherit;
}
ul,
ol {
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
}
img {
  vertical-align: bottom;
}
table {
  border-spacing: 0;
  border-collapse: collapse;
}
input,
input:focus {
  outline: none;
}

a {
  color: inherit;
  text-decoration: none;
}

.clear::after {
  display: block;
  content: "";
  clear: both;
}

.w100 {
  width: 100%;
}
.mw100 {
  max-width: 100%;
}

.wrapper {
  border-color: blue;
}

/* Banner slick-dots */
.slick-dots-banner {
  position: absolute;
  bottom: 1em;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
}
.slick-dots-banner li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}
.slick-dots-banner li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}
.slick-dots-banner li button:hover,
.slick-dots-banner li button:focus {
  outline: none;
}
.slick-dots-banner li button:hover:before,
.slick-dots-banner li button:focus:before {
  opacity: 1;
}
.slick-dots-banner li button:before {
  font-family: "slick";
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  text-align: center;
  font-size: 16px;
  line-height: 30px;
  width: 30px;
  height: 30px;
  opacity: 0.5;
  color: #ccc;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.slick-dots-banner li.slick-active button:before {
  opacity: 0.875;
  color: #28cdff;
}

